#root,
html,
body {
    height : 100vh;
    margin : 0;
    padding : 0;
}

body {
    margin : 0;
    background-color: rgba(235, 235,255, 0.7);
}

* {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

.app-container {
    width : 100%;
    height : 100%;
    position : relative;
    display : flex;
    flex-direction: column;
}

.header {
    height : 80px;
    background-color: red;
}

.content {
    position : relative;
    display : flex;
    flex-direction : column;
    justify-content: center;
    align-items: center;
    height : 100%;
}
